const sizesScales: { readonly number: string } = [...Array(200)].reduce(
  (acc, _, i) => {
    const position = i + 1

    return {
      ...acc,
      [position]: `${position * 4}px`,
    }
  },
  {}
)

export const sizes = {
  'icon-200': '16px',
  'icon-300': '24px',
  'icon-400': '32px',
  ...sizesScales,
} as const

export type Sizes = keyof typeof sizes
